*{
  padding: 0;
  box-sizing: border-box;
  margin: 0;
}
.content{
  margin-top: 20px;
  height:100vh;
}
.register-page{
  display:flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-image: url('./images/pexels-karolina-grabowska-4386339.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.filters{
  display:flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 30px;
  box-shadow: 0 0 3px gray;


}
.anticon svg{
  font-size: 20px;
  cursor: pointer;
}
.active-icon{
  color:black;
}
.inactive-icon{
  color:gray;
}
.switch-icons{
  border: 1px solid rgba(0,0,0,0.677);
  border-radius: 5px;
  padding: 10px 15px;
  
}
#button{
  width: 100%;
    padding: 12px;
    margin: 10px;
    background-color: #040404;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s;
}
.inner{
  max-width: 400px;
  width: 100%;
  padding: 20px;
  border:5px;
  background-color: rgba(4, 3, 3, 0.9); /* Add a semi-transparent white background to the form for better readability */
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin-left: 150px;
}
.link{
  margin-left: 50px;
  color:#4f838e;
}
.heading{
  color:#fff;
  margin-left: 50px;
}
#logbtn{
 background-color: red;
}

.nav-item{
  padding: 5px;
}
.main{
  text-align: center;
  color:white;
  justify-content: center;
  margin-left: 550px;
}
#bg{
  background-color: #040404;
}
#user{
  color:#fff;
}
.outer{
  background-color: #e7e8e9;

}

.row {
  display: flex;
  margin: -15px; /* Adjust margin to handle default column gutters */
}

/* Card styling */
.card {
  border: 1px solid #ddd;
  border-radius: 8px;
  margin-bottom: 20px;
  overflow: hidden;
}

.card-header {
  background-color: #007bff;
  color: #fff;
  padding: 5px;
}

.card-body {
  padding: 20px;
}
.col-md-3{
  width: 360px;
  margin-left: 12px;
  padding-left: 5px;
}
.custom-label{
  color:#fff;
}
